.bottom-menu {
  position: relative;
  height: 300px;
  grid-area: bottom-menu;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--component-bg-clr);
  border-top: 1px solid var(--component-border-clr);
}

.bottom-menu-resizer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  cursor: row-resize;
  transition: var(--base-transition);
}

.bottom-menu-resizer:hover {
  background-color: var(--accent-clr);
}